#static-image {
    width: 100vw;
    height: 100vh;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}

.start-screen{
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.start-screen h1{
    position: relative;
    text-align: center;
    color: white;
}

.center-modal{
    background-color: rgba(51, 51, 51, 0.32);
    border-radius: 40px;
    width: 400px;
    margin: auto;
}

@media only screen and (max-width: 600px) {
    .center-modal{
        background-color: rgba(51, 51, 51, 0.32);
        border-radius: 40px;
        width: 200px;
        margin: auto;
    }
    .start-screen h1{
        font-size: 20px;
    }
}